import React from 'react';
import Link from 'next/link';

function LinkButton(props) {
	const { href, text, color, backgroundColor, borderColor, hoverColor, hoverBackgroundColor } = props;
	return(
		<div>
			<Link href={href}>
				<button type="button">{text}</button>
			</Link>
      <style jsx>{`
				button{
          color: ${color||"#fff"};
          padding: 10px;
          cursor: pointer;
          font-size: 18px;
          font-weight: bold;
          border-radius: 5px;
          background: ${backgroundColor||"#111317"};
          border: 3px solid ${borderColor||"#070708"};
          transition-duration: 0.4s;
          -o-transition-duration: 0.4s; /* Opera */
          -moz-transition-duration: 0.4s; /* Mozilla */
          -webkit-transition-duration: 0.4s; /* Safari */
        }
        button:hover,
        button:active
        {
          color: ${hoverColor||"#111317"};
          background: ${hoverBackgroundColor||"#fff"};
        }
      `}</style>
		</div>
	);
}

export default LinkButton;